import { fromPromise } from 'rxjs/internal/observable/fromPromise';

export class Order {
    get codigo() {
        return this._codigo;
    }

    set codigo(value) {
        this._codigo = value;
    }

    get descricao() {
        return this._descricao;
    }

    set descricao(value) {
        this._descricao = value;
    }

    get company_id() {
        return this._company_id;
    }

    set company_id(value) {
        this._company_id = value;
    }

    get client_id() {
        return this._client_id;
    }

    set client_id(value) {
        this._client_id = value;
    }

    get score() {
        return this._score;
    }

    set score(value) {
        this._score = value;
    }

    get orderStatus() {
        return this._orderStatus;
    }

    set orderStatus(value) {
        this._orderStatus = value;
    }

    constructor(codigo, descricao, company_id, client_id, score, orderStatus) {

        this._codigo = codigo;
        this._descricao = descricao;
        this._company_id = company_id;
        this._client_id = client_id;
        this._score = score;
        this._orderStatus = orderStatus;
    }

    onInit() {

    }

    createOrderStatusTable() {

        const template = `
        <table id="myTable" class=" table order-list">
            <thead>
            <tr>
                <td>Descrição</td>
                <td>Data</td>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td class="col-sm-4">
                    <input type="text" name="observacao" class="form-control" />
                </td>
                <td class="col-sm-4">

                    <div class="form-group">
                        <div class="input-group date">
                            <div class="input-group-addon">
                                <i class="fa fa-calendar"></i>
                            </div>
                            <div class="input-group date">
                                <input type='text' class="datepicker-here form-control" data-position="right top" />
                            </div>
                        </div>
                        <!-- /.input group -->
                    </div>


                </td>
                <td class="col-sm-2">
                    <a class="deleteRow"></a>
                </td>
            </tr>
            </tbody>
            <tfoot>
            <tr>
                <td colspan="5" style="text-align: left;">
                    <input type="button" class="btn btn-success" id="addrow" value="Adicionar acompanhamento" />
                </td>
            </tr>
            <tr>
            </tr>
            </tfoot>
        </table>
        `

        $('#order-status-table').append(template);

    }

    createOrderStatusTableRow() {

        const self = this;

        let counter = 0;

        $("#addrow").on("click", function () {

            const newRow = $('<tr>');

            const rowTemplate = `
                <td><input type="text" class="form-control" name="observacao"/></td>
                <td>
                    <div class="input-group date">
                    <div class="input-group-addon"><i class="fa fa-calendar"></i></div>
                    <input type="text" class="form-control pull-right datepicker" name="data">
                </td>
                <td><input type="button" class="ibtnDel btn btn-md btn-danger"  value="Delete"></td>
            `;

            newRow.append(rowTemplate);
            $("table.order-list").append(newRow);
            counter++;
        });

        //
        // if (selectPartnersData) {
        //     self.createSelect(selectPartnersData);
        //     return;
        // }
        // self.createSelect();
    }

    removeOrderStatusTableRow() {
        $("table.order-list").on("click", ".ibtnDel", function (event) {
            $(this).closest("tr").remove();
        });
    }

    /**
     *
     * @param companyId
     * @returns { Observable }
     */
    getPartnersCompany(companyId) {
        if (!companyId) return;

        return fromPromise(
            $.get(`http://localhost:8000/api/v1/partners/company/${companyId}`)
        );
    }

    transformResponseToSelectData(data) {

        const newData = $.map(data, function (obj) {
            obj.id = obj.id;
            obj.text = obj.name + ' (' + obj.email + ')';

            return obj;
        });

        return newData;
    }

    createSelect(data) {
        $('.pontuacaoSelect').select2({
            data: data
        })
    }
}



