import {Order} from "./Order";

let empresaId = null;
let selectPartnersData = [];

const order = new Order();
order.createOrderStatusTable();
order.createOrderStatusTableRow();
order.removeOrderStatusTableRow();


/***********************************************
 SCORE
 ************************************************/

let counterScore = 0;

$("#addScore").on("click", function () {
    var newRow = $("<tr>");
    var cols = "";

    cols += '<td><select name="pontuacao" class="form-control pontuacaoSelect" /></td>';
    cols += '<td><input type="text" class="form-control" name="score' + counterScore + '"/></td>';

    cols += '<td><input type="button" class="ibtnDel btn btn-md btn-danger "  value="Delete"></td>';
    newRow.append(cols);
    $("table.score-list").append(newRow);
    counterScore++;
});

$("table.score-list").on("click", ".ibtnDel", function (event) {
    $(this).closest("tr").remove();
    counterScore -= 1
});

$('#companySelect').on('select2:select', function (e) {
    const self = this;
    var data = e.params.data;
    if (!data.id) return;
    order.getPartnersCompany(data.id).subscribe(res => {
        const selectPartnersData = order.transformResponseToSelectData(res);
        order.createSelect(selectPartnersData);
    });
});



